import { _decorator, Component, Node, Collider2D, Contact2DType, math, Vec3, tween, IPhysics2DContact, Prefab, instantiate, macro, Animation, Scene } from 'cc';
import { GameCtrl } from './GameCtrl';
import { Plane } from './Plane';
import { AudioToggle } from './AudioToggle';
import { ButtonPlayScene } from './ButtonPlayScene';
const { ccclass, property } = _decorator;

@ccclass('Boss')
export class Boss extends Component {
    static instance: Boss;
    @property(Prefab)
    public listBulletB1: Prefab [] = [];
    @property(Prefab)
    public listBulletB3: Prefab [] = [];
    @property(Animation)
    public laserBullet: Animation = null;
    @property
    speed: number = 100;
    public bulletCont1 = [];
    public bulletCont2 = [];
    public bulletCont3 = [];
    public maxHealth1: number = 50;
    public currentHealth1: number = 50;
    public maxHealth2: number = 100;
    public currentHealth2: number = 100;
    public maxHealth3: number = 150;
    public currentHealth3: number = 150;  
    public randomX: number;
    public randomY: number;
    public randomPosition: Vec3 = Vec3.ZERO;
    start() {
        let collider = this.getComponent(Collider2D);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
        this.schedule(this.spawnBullet, 2.5, macro.REPEAT_FOREVER, 0);
        this.randomPos();
        // this.moveRandom();
    }
    protected onLoad(): void {
        Boss.instance = this;
        
        
    }
    update(dt: number) {
        if (!ButtonPlayScene.instance.moveEnable) {
            return;
        }
        let direction = this.randomPosition.clone().subtract(this.node.position).normalize();
        let distance = math.Vec3.distance(this.node.position, this.randomPosition);
        let posX = this.node.position.x + direction.x * distance * 0.01;
        let posY = this.node.position.y + direction.y * distance * 0.01;
        this.node.setPosition(new Vec3(posX, posY));
        if (distance <= 50) {
            this.randomPos();   
        }
    }

    randomPos(){
        
        this.randomX = math.random() * (0 - 1000) + 500;
        this.randomY = math.random() * (200 - 600) + 200;
        this.randomPosition = new Vec3(this.randomX, this.randomY);
        console.log(this.randomPosition);
        
    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        AudioToggle.instance.onAudioQueue(7);

        if(otherCollider.node.name === 'bullet0' || otherCollider.node.name.startsWith('rocket')){
            if(this.node.name === 'boss001'){
                this.scheduleOnce(() => {this.bulletCont1.push(otherCollider)});
                this.currentHealth1 -= 1;
                if (this.currentHealth1 <= 0) {
                    this.currentHealth1 = 0;
                }
                this.updateHealthBar();
                if(this.bulletCont1.length >= 50){
                    this.scheduleOnce(() => {GameCtrl.instance.countPoint(1000)});
                    this.scheduleOnce(() => {selfCollider.node.destroy()});                    
                }
            }
            if(this.node.name === 'boss002'){
                this.scheduleOnce(() => {this.bulletCont2.push(otherCollider)});
                this.currentHealth2 -= 1;
                if (this.currentHealth2 <= 0) {
                    this.currentHealth2 = 0;
                }
                this.updateHealthBar();
                if(this.bulletCont2.length >= 100){
                    this.scheduleOnce(() => {GameCtrl.instance.countPoint(1000)});
                    this.scheduleOnce(() => {selfCollider.node.destroy()});
                }
            }
            if(this.node.name === 'boss003'){
                this.scheduleOnce(() => {this.bulletCont3.push(otherCollider)});
                this.currentHealth3 -= 1;
                if (this.currentHealth3 <= 0) {
                    this.currentHealth3 = 0;
                }
                this.updateHealthBar();
                if(this.bulletCont3.length >= 150){
                    this.scheduleOnce(() => {GameCtrl.instance.countPoint(1000)});
                    this.scheduleOnce(() => {selfCollider.node.destroy()});
                }
            }
            if(otherCollider.node.name.startsWith('bullet') || otherCollider.node.name.startsWith('rocket')){
                this.scheduleOnce(() => {otherCollider.node?.destroy()});
            }
            
        }
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
    spawnBullet(){
        if(!ButtonPlayScene.instance.spawnEnabled) return;
        if(this.node.name === 'boss001'){
            for(let i = 0; i < this.listBulletB1.length; i++){
            let enemyPre = this.listBulletB1[i];
            let prefabInstance = instantiate(enemyPre);
            prefabInstance.parent = this.node.parent;
            prefabInstance.setWorldPosition(this.node.worldPosition);
            }
        }
        if(this.node.name === 'boss002'){
            return;
        }
        if(this.node.name === 'boss003'){
            for( let i = 0; i < this.listBulletB3.length; i++){
                for(let j = 0; j < 5; j++){
                    let enemyPre = this.listBulletB3[i];
                    let prefabInstance = instantiate(enemyPre);
                    prefabInstance.parent = this.node.parent;
    
                    if(enemyPre.name.endsWith('_0') || enemyPre.name.endsWith('_1') || enemyPre.name.endsWith('_2') || enemyPre.name.endsWith('_3') || enemyPre.name.endsWith('_4')){
                        prefabInstance.setWorldPosition(this.node.worldPosition.x - 105, this.node.worldPosition.y - 65, this.node.worldPosition.z);
                    }
                    if(enemyPre.name.endsWith('_5') || enemyPre.name.endsWith('_6') || enemyPre.name.endsWith('_7') || enemyPre.name.endsWith('_8') || enemyPre.name.endsWith('_9')){
                        prefabInstance.setWorldPosition(this.node.worldPosition.x + 105, this.node.worldPosition.y - 65, this.node.worldPosition.z);
                    }
                }
            }
        }
    }
    updateHealthBar() {
        // if (GameCtrl.instance.healthBar[0]) {
        //     GameCtrl.instance.healthBar[0].progress = this.currentHealth1 / this.maxHealth1;
        // }if (GameCtrl.instance.healthBar[1]) {
        //     GameCtrl.instance.healthBar[1].progress = this.currentHealth2 / this.maxHealth2;
        // }if (GameCtrl.instance.healthBar[2]) {
        //     GameCtrl.instance.healthBar[2].progress = this.currentHealth3 / this.maxHealth3;
        // }
    }
}


